import React, { useState, useEffect, useCallback } from 'react';
import { Container, Form, Button, Table, Spinner, Alert, Row, Col } from 'react-bootstrap';
import axios from 'axios';
import { getTodayString, formatDate, formatNumber, formatDateTime } from './utils/dateUtils';

function Transaction() {
  const [startDate, setStartDate] = useState(getTodayString());
  const [endDate, setEndDate] = useState(getTodayString());
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [selectedStores, setSelectedStores] = useState(['BLUESQUARE', 'GSartcenter']); // :   

  const API_BASE_URL = 'https://9w3707plqb.execute-api.ap-northeast-2.amazonaws.com/default/orders/search';
  const BRAND_COLOR = '#FF7300';
  const storeOptions = ['BLUESQUARE', 'GSartcenter']; //   

  const fetchTransactions = useCallback(async () => {
    setLoading(true);
    setError('');
    try {
      const allTransactions = [];
      //    API  
      if (selectedStores.length === 0) {
        setTransactions([]);
        setLoading(false);
        return;
      }

      const fetchPromises = selectedStores.map(async (store) => { // selectedStores 
        const response = await axios.get(API_BASE_URL, {
          params: {
            store_name: store,
            start_date: startDate,
            end_date: endDate,
            summary: 'false'
          },
        });
        return response.data;
      });

      const results = await Promise.all(fetchPromises);
      results.forEach(data => allTransactions.push(...data));

      // created_at ( order_date)   
      allTransactions.sort((a, b) => {
        const dateA = new Date(a.created_at || a.order_date);
        const dateB = new Date(b.created_at || b.order_date);
        return dateB - dateA;
      });

      setTransactions(allTransactions.map((currentTx, index, arr) => {
        const nextTx = arr[index + 1];
        let isErrorSuspected = false;
        let remark = '';

        if (nextTx) {
          const currentCreated = new Date(currentTx.created_at || currentTx.order_date).getTime();
          const nextCreated = new Date(nextTx.created_at || nextTx.order_date).getTime();
          const timeDiff = Math.abs(currentCreated - nextCreated); //   

          if (
            currentTx.store_name === nextTx.store_name &&
            currentTx.photo_detail_type === nextTx.photo_detail_type &&
            timeDiff <= 20 * 1000 // 20 
          ) {
            isErrorSuspected = true;
            remark = '';
          }
        }
        return { ...currentTx, isErrorSuspected, remark };
      }));
    } catch (err) {
      setError('    .');
      console.error(err);
      setTransactions([]);
    }
    setLoading(false);
  }, [startDate, endDate, selectedStores]); // selectedStores   

  useEffect(() => {
    fetchTransactions();
  }, [fetchTransactions]);

  const handleSearch = (e) => {
    e.preventDefault();
    fetchTransactions();
  };

  const handleStoreToggle = (value) => {
    const newSelectedStores = selectedStores.includes(value)
      ? selectedStores.filter(store => store !== value)
      : [...selectedStores, value];
    setSelectedStores(newSelectedStores);
  };

  return (
    <Container className="mt-4">
      <h2></h2>

      <div className="mb-4">
        {storeOptions.map((name) => (
          <Form.Check
            inline
            key={name}
            type="checkbox"
            id={`store-${name}`}
            label={name}
            checked={selectedStores.includes(name)}
            onChange={() => handleStoreToggle(name)}
          />
        ))}
      </div>

      <Form onSubmit={handleSearch} className="mb-4 p-4 border rounded bg-light">
        <Row className="gy-3 align-items-center">
          <Col md={6} lg={4}>
            <Form.Group controlId="transactionStartDate">
              <Form.Label className="mt-2"></Form.Label>
              <Form.Control
                type="date"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
                required
              />
            </Form.Group>
          </Col>
          <Col md={6} lg={4}>
            <Form.Group controlId="transactionEndDate">
              <Form.Label className="mt-2"></Form.Label>
              <Form.Control
                type="date"
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
                required
              />
            </Form.Group>
          </Col>
          <Col lg={2} className="d-flex align-items-end mt-auto">
            <Button variant="primary" type="submit" disabled={loading} className="w-100" style={{ backgroundColor: BRAND_COLOR, borderColor: BRAND_COLOR }}>
              {loading ? <Spinner as="span" animation="border" size="sm" /> : ''}
            </Button>
          </Col>
        </Row>
      </Form>

      {error && <Alert variant="danger">{error}</Alert>}

      <Table striped bordered hover responsive>
        <thead>
          <tr>
            <th> </th>
            <th></th>
            <th> </th>
            <th>  </th>
            <th> </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {loading ? (
            <tr>
              <td colSpan="6" className="text-center"><Spinner/></td>
            </tr>
          ) : transactions.length > 0 ? (
            transactions.map((transaction) => (
              <tr key={transaction.order_id || transaction.created_at} style={{ backgroundColor: transaction.isErrorSuspected ? '#FFDDDD' : '' }}> {/*       */}
                <td>{transaction.store_name}</td>
                <td>{transaction.quantity || 'N/A'}</td>
                <td> {formatNumber(transaction.payment_amount || transaction.total_sales)}</td>
                <td>{transaction.photo_detail_type || 'N/A'}</td>
                <td>{formatDateTime(transaction.created_at)}</td>
                <td>
                  {transaction.remark === '' ? (
                    <span style={{ color: 'red', fontWeight: 'bold' }}>{transaction.remark}</span>
                  ) : (
                    transaction.remark
                  )}
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan="6" className="text-center">   .</td>
            </tr>
          )}
        </tbody>
      </Table>
    </Container>
  );
}

export default Transaction;